.variable-model{padding:10px 10px 5px}.variable-model .list{position:relative;min-height:30px}.variable-model .list .var-cont{padding-left:19px}.variable-model .list .var-cont>button{margin-bottom:6px;margin-right:6px}.variable-model .list .name{padding-bottom:10px;font-size:16px}.variable-model .list .name>.fa{font-size:16px;color:#0097e0;margin-right:4px;vertical-align:middle;font-weight:700}.variable-model .list .name>b{vertical-align:middle}.variable-model .list>span{height:28px;line-height:28px;padding:0 8px;background:#2d8cf0;display:inline-block;margin-bottom:8px;color:#fff}.variable-model .list-t{padding-left:0;margin-bottom:10px}.variable-model .list-t .task-name{padding-left:19px;padding-bottom:8px;font-size:12px;font-weight:700;color:#0097e0}