.udp-model{width:624px;min-height:420px;background:#fff;border-radius:3px;padding:20px 0;position:relative}.udp-model .contpi-boxt{max-height:600px;overflow-y:scroll;padding:0 20px}.udp-model .title{line-height:36px;padding-bottom:10px}.udp-model .title span{font-size:16px;color:#333}.udp-model .bottom{position:absolute;bottom:0;left:0;width:100%;text-align:right;height:56px;line-height:56px;border-top:1px solid #dcdedc;background:#fff}.udp-model .bottom .submit{padding-right:20px;margin-top:-4px}.udp-model .bottom .lint-pt{position:absolute;left:20px;top:-2px}.udp-model .bottom .lint-pt>label{font-weight:400}.udp-model .content{padding-bottom:50px}.udp-model .content .user-def-params-model .add a{color:#0097e0}