.log-pop{position:fixed;left:0;top:0;width:100%;height:100%;background:rgba(0,0,0,.4);z-index:10}.log-pop .log-box{width:660px;height:520px;background:#fff;border-radius:3px;position:absolute;left:50%;top:50%;margin-left:-340px;margin-top:-250px}.log-pop .log-box .title{height:50px;border-bottom:1px solid #dcdedc}.log-pop .log-box .title span{font-size:16px;color:#333;padding-left:20px;display:inline-block;padding-top:16px}.log-pop .log-box .title .full-screen{position:absolute;right:20px;top:12px}.log-pop .log-box .title .full-screen a{color:#0097e0;margin-left:10px}.log-pop .log-box .title .full-screen a em{font-size:17px;font-weight:400;text-decoration:none!important;vertical-align:middle}.log-pop .log-box .title .full-screen .clock>em,.log-pop .log-box .title .full-screen .refresh-log>em{font-size:20px;vertical-align:middle;-webkit-transform:scale(1);-ms-transform:scale(1);transform:scale(1)}.log-pop .log-box .title .full-screen .refresh-log>em{text-decoration:none}.log-pop .log-box .title .full-screen .refresh-log.active>em{-webkit-transition-property:-webkit-transform;-webkit-transition-duration:1s;-moz-transition-property:-moz-transform;-moz-transition-duration:1s;-webkit-animation:rotateloading .4s linear infinite;animation:rotateloading .4s linear infinite;-webkit-transform:scale(.4);-ms-transform:scale(.4);transform:scale(.4);color:#999}.log-pop .log-box .content{height:calc(100% - 100px);background:#002a35;padding:6px 2px}.log-pop .log-box .content .content-log-box{width:100%;height:100%;word-break:break-all}.log-pop .log-box .content .content-log-box textarea{background:none;color:#9cabaf;border:0;font-family:"Microsoft Yahei,Arial,Hiragino Sans GB,tahoma,SimSun,sans-serif";font-weight:700;resize:none;line-height:1.6;padding:0}.log-pop .log-box .operation{text-align:right;height:50px;line-height:44px;border-top:1px solid #dcdedc;padding-right:20px;background:#fff;position:relative}@-webkit-keyframes rotateloading{0%{-webkit-transform:rotate(0deg)}to{-webkit-transform:rotate(1turn)}}@keyframes rotateloading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(359deg);transform:rotate(359deg)}}