.dependent-model{margin-top:-10px}.dependent-model .dep-opt{margin-bottom:10px;padding-top:3px;line-height:24px}.dependent-model .dep-opt .add-dep{color:#0097e0;margin-right:10px}.dependent-model .dep-opt .add-dep i{font-size:18px;vertical-align:middle}.dependent-model .dep-list{margin-bottom:16px;position:relative;border-left:1px solid #eee;padding-left:16px;margin-left:-16px;padding-bottom:20px}.dependent-model .dep-list,.dependent-model .dep-list:hover{-webkit-transition:all .2s ease-out;transition:all .2s ease-out}.dependent-model .dep-list:hover{border-left:1px solid #0097e0}.dependent-model .dep-list:hover .dep-line-pie{-webkit-transition:all .2s ease-out;transition:all .2s ease-out;border:1px solid #0097e0;background:#0097e0;color:#fff}.dependent-model .dep-list .dep-line-pie{-webkit-transition:all .2s ease-out;transition:all .2s ease-out;position:absolute;width:20px;height:20px;border:1px solid #e2e2e2;text-align:center;top:50%;margin-top:-20px;z-index:1;left:-10px;border-radius:10px;background:#fff;font-size:12px;cursor:pointer}.dependent-model .dep-list .dep-line-pie::selection{background:rgba(0,0,0,0)}.dependent-model .dep-list .dep-line-pie::-moz-selection{background:rgba(0,0,0,0)}.dependent-model .dep-list .dep-line-pie::-webkit-selection{background:rgba(0,0,0,0)}.dependent-model .dep-list .dep-delete{position:absolute;bottom:-6px;left:14px;font-size:18px;color:red;cursor:pointer}.dependent-model .dep-box{border-left:4px solid #eee;margin-left:-46px;padding-left:42px;position:relative}.dependent-model .dep-box .dep-relation{position:absolute;width:20px;height:20px;border:1px solid #e2e2e2;text-align:center;top:50%;margin-top:-10px;z-index:1;left:-12px;border-radius:10px;background:#fff;font-size:12px;cursor:pointer}.dependent-model .dep-box .dep-relation::selection{background:rgba(0,0,0,0)}.dependent-model .dep-box .dep-relation::-moz-selection{background:rgba(0,0,0,0)}.dependent-model .dep-box .dep-relation::-webkit-selection{background:rgba(0,0,0,0)}