.container{width:500px;position:relative}.container .title-box{height:61px;border-bottom:1px solid #dcdedc;position:relative}.container .title-box .name{position:absolute;left:24px;top:18px;font-size:16px}.container .bottom-box{position:absolute;bottom:0;left:0;width:100%;text-align:right;height:60px;line-height:60px;border-top:1px solid #dcdedc;background:#fff}.container .bottom-box .ans-page{display:inline-block}.container .table-box{overflow-y:scroll;height:calc(100vh - 61px);padding-bottom:60px}