.dep-list-model{position:relative;min-height:32px}.dep-list-model .list{margin-bottom:6px}.dep-list-model .list .operation{padding-left:4px}.dep-list-model .list .operation a i{font-size:18px;vertical-align:middle}.dep-list-model .list .operation .delete{color:red}.dep-list-model .list .operation .add{color:#0097e0}.dep-list-model .instance-state{display:inline-block;width:24px}.dep-list-model .instance-state .iconfont{font-size:20px;vertical-align:middle;cursor:pointer;margin-left:6px}.dep-list-model .instance-state .iconfont.icon-SUCCESS{color:#3c0}.dep-list-model .instance-state .iconfont.icon-WAITING{color:#888}.dep-list-model .instance-state .iconfont.icon-FAILED{color:#f31322}